import {
  Dispatch,
  RefObject,
  SetStateAction,
  useCallback,
  useRef,
  useState,
} from "react";

export function useStateRef<T>(
  initialValue: T
): [T, Dispatch<SetStateAction<T>>, RefObject<T>] {
  const [value, setValue] = useState<T>(initialValue);
  const valueRef = useRef<T>(initialValue);

  return [
    value,
    (v: SetStateAction<T>) =>
      setValue((prevValue) => {
        const newValue = typeof v === "function" ? (v as any)(prevValue) : v;
        valueRef.current = newValue;
        return newValue;
      }),
    valueRef,
  ];
}

export function useCallOnce<T extends (...args: unknown[]) => unknown>(
  cb: T,
  deps: any[]
): T {
  const called = useRef(false);
  return useCallback(
    ((...args: Parameters<T>) => {
      if (!called.current) {
        called.current = true;
        return cb(...args);
      }
      return undefined;
    }) as any,
    deps
  );
}
