import React, { useEffect, useRef, useState } from "react";
import styled from "@emotion/styled/macro";
import { animate, motion, useMotionValue, useTransform } from "framer-motion";
import { wrap } from "popmotion";
import { YugiohSet } from "../utils/yugioh";

const cardHeight = 300;
const cardWidth = 160;

const WheelContainer = styled.div({
  overflow: "hidden",
  display: "flex",
  flexFlow: "column nowrap",
  position: "relative",

  width: cardWidth,
  height: cardHeight,

  ">.inner": {
    position: "absolute",
  },
});

const WheelEntry = styled.div({
  position: "relative",
  width: cardWidth,
  height: cardHeight,
  backgroundColor: "#fff",

  ">img": {
    position: "absolute",
    top: "50%",
    transform: "translate(0, -50%)",
    width: cardWidth,
  },
});

interface WheelProps {
  className?: string;
  sets: YugiohSet[];
  index: number;
  show?: boolean;
  downwards?: boolean;
}
interface WheelProps {
  className?: string;
  sets: YugiohSet[];
  index: number;
  show?: boolean;
  downwards?: boolean;
}

function Wheel({ className, sets, index, show }: WheelProps) {
  const yPos = useMotionValue(cardHeight);
  const wrapNum = useRef(sets.length * cardHeight);
  const y = useTransform(yPos, (y) => wrap(0, -wrapNum.current, y));

  wrapNum.current = sets.length * cardHeight;

  useEffect(() => {
    if (show) {
      const toPos = -(1 + index) * cardHeight;
      yPos.set(toPos - cardHeight * 40);
      animate(yPos, toPos, {
        type: "tween",
        ease: [0.32, 0, 0, 1],
        duration: 6,
      });
    }
  }, [show, index, yPos]);

  return (
    <WheelContainer className={className}>
      <motion.div
        className="inner"
        style={{ y }}
        initial={{ opacity: 0 }}
        animate={{ opacity: show ? 1 : 0 }}
      >
        {sets.length > 0 && (
          <WheelEntry>
            <img
              alt={sets[sets.length - 1].name}
              src={sets[sets.length - 1].image}
            />
          </WheelEntry>
        )}
        {sets.map((e) => (
          <WheelEntry key={e.name}>
            <img alt={e.name} src={e.image} />
          </WheelEntry>
        ))}
        {sets.length > 0 && (
          <WheelEntry>
            <img alt={sets[0].name} src={sets[0].image} />
          </WheelEntry>
        )}
      </motion.div>
    </WheelContainer>
  );
}

const WheelWrapper = styled.div({
  position: "relative",
  display: "flex",
  flexFlow: "column",
  alignItems: "center",
  width: 220,

  ">.wheel": {
    position: "relative",
    zIndex: 5,
    marginTop: 8,
  },
  ">.name": {
    fontWeight: "bold",
    fontSize: 20,
    textAlign: "center",
  },
  ">.spin": {
    position: "absolute",
    zIndex: 1,
    top: "50%",
    marginTop: "-1em",
    height: "2em",
    left: 0,
    width: "100%",
    textAlign: "center",
    fontSize: 20,
  },
  ">.result": {
    marginTop: 8,
    fontSize: 20,
    textAlign: "center",
    height: "3em",
  },
});

export interface WheelChooserProps {
  name: string;
  wheel: YugiohSet[];
  onIndexChange: (index: number) => void;
}

export function WheelChooser({
  name,
  wheel,
  onIndexChange,
}: WheelChooserProps) {
  const [index, setIndex] = useState(0);
  const [showName, setShowName] = useState(false);
  const [show, setShow] = useState(false);

  return (
    <WheelWrapper
      onClick={() => {
        if (!show) {
          const choice = Math.floor(Math.random() * wheel.length);
          setIndex(choice);
          setShow(true);
          setTimeout(() => {
            setShowName(true);
            onIndexChange(choice);
          }, 6000);
        }
      }}
    >
      <div className="name">{name}</div>
      <Wheel className="wheel" show={show} sets={wheel} index={index} />
      <div className="spin">Spin!</div>
      <motion.div
        className="result"
        initial={{ opacity: 0 }}
        animate={{ opacity: showName ? 1 : 0 }}
      >
        {wheel[index]?.name ?? " "}
      </motion.div>
    </WheelWrapper>
  );
}
