import _ from "lodash";
import { cardTypesExtra, rarityMap } from "../data";

export interface YugiohSetKind {
  name: string;
  sets: YugiohSet[];
}
export interface YugiohSet {
  name: string;
  set: string;
  type: number;
  image: string;
}
export interface YugiohCard {
  id: number;
  name: string;
  extra: boolean;
  type: string;
  rarity: string;
  image: string;
}

export function corsUrl(url: string) {
  return `https://n1xx1-cors-everywhere.herokuapp.com/${url}`;
}

export async function serviceOpenPack(set: YugiohSet) {
  const baseYpd = `https://db.ygoprodeck.com/queries/pack-opener/pack-open.php`;
  const encodedName = encodeURIComponent(set.name);
  const url = `${baseYpd}?format=${encodedName}&settype=${set.type}`;

  const resp = await fetch(corsUrl(url));
  const cards = (await resp.json())[0];

  const ret: YugiohCard[] = cards.map((c: any) => ({
    id: c.id,
    name: c.name,
    extra: cardTypesExtra.includes(c.type),
    type: c.type,
    rarity: rarityMap[c.rarity]?.abbr ?? c.rarity,
    image: `https://storage.googleapis.com/ygoprodeck.com/pics/${c.id}.jpg`,
  }));
  return ret;
}

export const packData: Record<
  string,
  YugiohSetKind
> = require("../packData.json");
