import React, {
  CSSProperties,
  Dispatch,
  memo,
  ReactNode,
  SetStateAction,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import styled from "@emotion/styled/macro";
import { StepValues } from "./app";
import { Button } from "./button";
import _ from "lodash";
import { cardTypesSort } from "../data";
import { motion, Variants } from "framer-motion";
import { createPortal } from "react-dom";
import { YugiohCard } from "../utils/yugioh";
import { downloadFile } from "../utils/download-file";

const cardRatio = 421 / 614;

const ViewContainer = styled(motion.div)({
  display: "flex",
  flexFlow: "row wrap",
  marginTop: -20,
});

const DetailContainer = styled.div({
  flex: "0 0 auto",
  width: 300,
  display: "flex",
  flexFlow: "column",
  justifyContent: "center",
  alignItems: "center",

  ">.image": {
    width: 280,
    height: 280 / cardRatio,

    ">img": {
      display: "block",
      width: "100%",
      height: "100%",
    },
  },
});

const CardsContainer = styled(motion.div)({
  flex: "1 1 0%",
  overflow: "auto",
  display: "flex",
  flexFlow: "row wrap",
  maxHeight: 500,
  justifyContent: "center",
  padding: "20px 0",

  ">img": {
    flex: "0 0 auto",
    width: 50,
    height: 50 / cardRatio,
    margin: 5,
  },
});

const ButtonsContainer = styled.div({
  display: "flex",
  flexFlow: "row wrap",
  alignItems: "center",
  justifyContent: "center",
});

export interface StepViewProps {
  stepValues: StepValues;
  onDone(): void;
}

const cardsViewVariants: Variants = {
  hidden: { opacity: 0 },
  show: { opacity: 1, transition: { staggerChildren: 0.05 } },
};
const cardVariants: Variants = {
  hidden: { opacity: 0 },
  show: { opacity: 1 },
  ignored: { opacity: 0.5 },
};

interface CardListCard extends YugiohCard {
  ignore: boolean;
}

interface CardListProps {
  cards: CardListCard[];
  setCurrentDetail: Dispatch<SetStateAction<YugiohCard | undefined>>;
  onIgnore(index: number): void;
}

const CardList = memo(
  ({ cards, setCurrentDetail, onIgnore }: CardListProps) => {
    return (
      <CardsContainer
        variants={cardsViewVariants}
        initial="hidden"
        animate="show"
      >
        {cards.map((c, i) => (
          <motion.img
            layout
            variants={cardVariants}
            key={i}
            src={c.image.replace(/\/pics\//g, "/pics_small/")}
            animate={c.ignore ? "ignored" : undefined}
            onMouseEnter={() => setCurrentDetail(c)}
            onClick={() => onIgnore(i)}
          />
        ))}
      </CardsContainer>
    );
  }
);

function createYdk(
  main?: YugiohCard[],
  side?: YugiohCard[],
  extra?: YugiohCard[]
) {
  let ydk = "#created by ArenaDraft\n#main\n";
  main?.forEach((c) => (ydk += `${c.id}\n`));
  ydk += "#extra\n";
  extra?.forEach((c) => (ydk += `${c.id}\n`));
  ydk += "!side\n";
  main?.forEach((c) => (ydk += `${c.id}\n`));
  return ydk;
}

export function StepView({ stepValues, onDone }: StepViewProps) {
  const [currentDetail, setCurrentDetail] = useState<YugiohCard>();
  const [cards, setCards] = useState<CardListCard[]>(() => {
    const cards = stepValues.step1!.cards;
    return _.orderBy(
      cards,
      [(c) => cardTypesSort[c.type] ?? 20, "name"],
      ["asc", "asc"]
    ).map((c) => ({ ...c, ignore: false }));
  });

  const ydkCount = useMemo(() => {
    const deckCount = cards.filter((c) => !c.ignore && !c.extra).length!;
    const extraCount = cards.filter((c) => !c.ignore && c.extra).length!;
    return Math.max(
      Math.ceil(deckCount / (60 + 15)),
      Math.ceil(extraCount / 15)
    );
  }, [cards]);

  const handleIgnore = useCallback(
    (index: number) => {
      setCards(
        cards
          .slice()
          .map((c, i) => (index == i ? { ...c, ignore: !c.ignore } : c))
      );
    },
    [cards]
  );

  const handleClick = useCallback(
    (single: boolean) => {
      const main = cards.filter((c) => !c.ignore && !c.extra);
      const extra = cards.filter((c) => !c.ignore && c.extra);

      if (single) {
        const ydk = createYdk(main, [], extra);
        downloadFile(ydk, `draft.ydk`);
        return;
      }

      const mainChunks = _.chunk(main, 75);
      const extraChunks = _.chunk(extra, 15);
      const ydks = _.zip(mainChunks, extraChunks);

      for (let i = 0; i < ydks.length; i++) {
        const [main, extra] = ydks[i];
        const ydk = createYdk(main?.slice(0, 60), main?.slice(60), extra);
        setTimeout(() => downloadFile(ydk, `draft_${i + 1}.ydk`), i * 500);
      }
    },
    [cards]
  );

  return (
    <>
      <ViewContainer layout>
        <DetailContainer>
          <div className="image">
            {currentDetail && <img src={currentDetail.image} />}
          </div>
        </DetailContainer>
        <CardList
          cards={cards}
          setCurrentDetail={setCurrentDetail}
          onIgnore={handleIgnore}
        />
      </ViewContainer>
      <ButtonsContainer>
        <Button onClick={() => handleClick(false)}>
          YDKs for DuelingBook ({ydkCount})
        </Button>
        <Button onClick={() => handleClick(true)}>YDK for EdoPro</Button>
      </ButtonsContainer>
    </>
  );
}
