import React, { ComponentType, FC, useState } from "react";
import styled from "@emotion/styled/macro";
import { StepWheels, StepWheelsResult } from "./step-wheels";
import {
  AnimatePresence,
  AnimateSharedLayout,
  motion,
  Variants,
} from "framer-motion";
import { StepPacks, StepPacksResult } from "./step-packs";
import { StepView } from "./step-view";

const Content = styled(motion.div)({
  display: "flex",
  height: "100%",
  flexFlow: "column",
  alignItems: "center",
  justifyContent: "center",
});

const Frame = styled(motion.div)({
  position: "relative",
  width: 1000,
  padding: "20px 0",
  display: "flex",
  backgroundColor: "#fff",
  flexFlow: "column",
  overflow: "hidden",
});

const StepsContainer = styled(motion.div)({
  width: 1000,
  display: "flex",
  flexFlow: "row nowrap",
  backgroundColor: "#b2591a",
  color: "#ffffff",
  overflow: "hidden",

  ".name": {
    flex: 1,
    textAlign: "center",
    fontSize: 16,
    padding: 5,
  },
});

interface StepsProps {
  stepName: string;
}

export function Steps({ stepName }: StepsProps) {
  return (
    <StepsContainer layout>
      <AnimatePresence exitBeforeEnter={true} initial={false}>
        <motion.div
          initial="initial"
          animate="enter"
          exit="exit"
          variants={stepVariants}
          key={stepName}
          className="name"
        >
          {stepName}
        </motion.div>
      </AnimatePresence>
    </StepsContainer>
  );
}

const stepVariants: Variants = {
  enter: { opacity: 1 },
  exit: { x: "-50%", opacity: 0 },
  initial: { opacity: 0 },
};

interface StepData<T extends { onDone: any; stepValues?: StepValues }> {
  name: string;
  component: ComponentType<T>;
}

const steps: StepData<any>[] = [
  { name: "Roll Packs", component: StepWheels },
  { name: "Open Packs", component: StepPacks },
  { name: "View Cards", component: StepView },
];
export interface StepValues {
  step0?: StepWheelsResult;
  step1?: StepPacksResult;
}

export function App({}: {}) {
  const [stepValues, setStepValues] = useState<StepValues>({});
  const [step, setStep] = useState(0);

  return (
    <AnimateSharedLayout>
      <Content layout>
        <Steps stepName={steps[step].name} />
        <Frame layout>
          <AnimatePresence exitBeforeEnter={true} initial={false}>
            {steps.map(
              ({ component: Component }, i) =>
                step == i && (
                  <motion.div
                    layout
                    key={"step" + 0}
                    initial="initial"
                    animate="enter"
                    exit="exit"
                    variants={stepVariants}
                  >
                    <Component
                      stepValues={stepValues}
                      onDone={(result: any) => {
                        setStepValues(
                          (v) => ({ v, ["step" + i]: result } as any)
                        );
                        setStep(i + 1);
                      }}
                    />
                  </motion.div>
                )
            )}
          </AnimatePresence>
        </Frame>
      </Content>
    </AnimateSharedLayout>
  );
}
