import React, { useEffect, useState } from "react";
import {
  dataBattleWheel,
  dataMegaWheel,
  dataModernWheel,
  dataOldWheel,
} from "../data";
import { packData, YugiohSet } from "../utils/yugioh";
import { WheelChooser } from "./wheel";
import { motion } from "framer-motion";
import styled from "@emotion/styled/macro";
import _ from "lodash";
import { Button } from "./button";
import { skipStepWheels } from "../config";

const Wheels = styled.div({
  flex: 1,
  display: "flex",
  alignItems: "center",
  justifyContent: "space-around",
});

export interface StepWheelsResult {
  modern: YugiohSet;
  old: YugiohSet;
  mega: YugiohSet;
  battle: YugiohSet;
}

export interface StepWheelsProps {
  onDone(result: StepWheelsResult): void;
}

export function StepWheels({ onDone }: StepWheelsProps) {
  const [indexes, setIndexes] = useState({
    modern: -1,
    old: -1,
    mega: -1,
    battle: -1,
  });

  const [wheelData, setWheelData] = useState<{
    modern: YugiohSet[];
    old: YugiohSet[];
    mega: YugiohSet[];
    battle: YugiohSet[];
  } | null>(null);

  useEffect(() => {
    loadData();
  }, []);

  const completed =
    indexes.modern >= 0 &&
    indexes.old >= 0 &&
    indexes.mega >= 0 &&
    indexes.battle >= 0;

  return (
    <>
      <Wheels>
        <WheelChooser
          name="Modern"
          wheel={wheelData?.modern ?? []}
          onIndexChange={(index) =>
            setIndexes((i) => ({ ...i, modern: index }))
          }
        />
        <WheelChooser
          name="Old"
          wheel={wheelData?.old ?? []}
          onIndexChange={(index) => setIndexes((i) => ({ ...i, old: index }))}
        />
        <WheelChooser
          name="Mega"
          wheel={wheelData?.mega ?? []}
          onIndexChange={(index) => setIndexes((i) => ({ ...i, mega: index }))}
        />
        <WheelChooser
          name="Battle"
          wheel={wheelData?.battle ?? []}
          onIndexChange={(index) =>
            setIndexes((i) => ({ ...i, battle: index }))
          }
        />
      </Wheels>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: completed ? 1 : 0 }}
      >
        <Button onClick={handleClick}>Continue</Button>
      </motion.div>
    </>
  );

  function handleClick() {
    if (completed) {
      onDone({
        modern: wheelData!.modern[indexes.modern],
        old: wheelData!.old[indexes.old],
        mega: wheelData!.mega[indexes.mega],
        battle: wheelData!.battle[indexes.battle],
      });
    }
  }

  async function loadData() {
    const ocgSets = [
      "ocg_boosters",
      "ocg_boosters_series1",
      "ocg_boosters_volume1",
    ];
    const sets = packData;
    const allSets = _.flatMap(sets, (s) => s.sets).filter(
      (s) => !ocgSets.includes(s.set)
    );

    setWheelData({
      modern: dataModernWheel.map((w) => allSets.find((s) => s.name === w)!),
      old: dataOldWheel.map((w) => allSets.find((s) => s.name === w)!),
      mega: dataMegaWheel.map((w) => allSets.find((s) => s.name === w)!),
      battle: dataBattleWheel.map((w) => allSets.find((s) => s.name === w)!),
    });

    if (skipStepWheels) {
      setIndexes({
        modern: Math.floor(Math.random() * dataModernWheel.length),
        old: Math.floor(Math.random() * dataOldWheel.length),
        mega: Math.floor(Math.random() * dataMegaWheel.length),
        battle: Math.floor(Math.random() * dataBattleWheel.length),
      });
    }
  }
}
